import { useState, useEffect } from "react";
import Map from "./components/Map";
import Loader from "./components/Loader";
import Header from "./components/Header";
import ThemeButton from "./components/ThemeButton";
import EventSelector from "./components/EventSelector";

function App() {
  const [eventData, setEventData] = useState([]);
  const [loading, setLoading] = useState(false);
  const [theme, setTheme] = useState(false);
  const [eventList, setEventList] = useState(['wildfires']);

  useEffect(() => {
    const fetchEvents = async () => {
      setLoading(true);
      const res = await fetch("https://eonet.sci.gsfc.nasa.gov/api/v3/events");
      const { events } = await res.json();

      setEventData(events);
      setLoading(false);
    };
    fetchEvents();
  }, []);


  return (
    <div>
      <Header />
      {!loading ? 
      <>
      <Map eventData={eventData} theme={theme} eventList={eventList} />
      <ThemeButton theme={theme} setTheme={setTheme} />
      <EventSelector eventList={eventList} setEventList = {setEventList} />
      </> : <Loader />}
      
    </div>
  );
}

export default App;
